const NATIVE_NAME = 'AHRCUAPP';
const JSBRIDGE_NAME = 'JSBridge';
const CALLBACK_NAME = 'callbackId';
const IS_IN_APP = window.navigator.userAgent.indexOf('AHRCU') > -1;
const CTX = 'http://twoer.github.io/demo/ahrcu/';
// window.location.origin || window.location.protocol + "//" + window.location.hostname + (window.location.port ? ':' + window.location.port : '');
console.log(`IS_IN_APP: ${IS_IN_APP}`);

const JSBridge = {
    callbackList: {},
    subEventList: {},
    mixinURL: function( url ){
        return CTX + '/#' + url;
    },
    callbackId: function(){
        return ((+new Date() * (Math.random() + 1 ) + (1 << Math.ceil(Math.random() * 10 + 20)))).toFixed();
    },
    call: function(data) {
        data = data || {};
        let _method = data.method;
        let _params = JSON.stringify( data.params || null );
        let _callback = data.callback;
        let _callbackId = this.callbackId();
        if( !_method ) {
            console.log( 'method is not string' );
            return;
        }
        if( _callback && typeof _callback !== 'function' ) {
            console.log( 'callback is not function' );
            return;
        }
        if( _params === '{}' || _params === 'null' ) {
            _params = '';
        }
        console.log(`method: ${_method}, params: ${_params}, callback: ${_callback}, callbackId: ${_callbackId}`);
        this.callbackList[ _callbackId ] = _callback;
        let _url = `${NATIVE_NAME}://${_method}:${_callbackId}?${_params && encodeURIComponent(_params)}`;
        console.log(_url);
        if( IS_IN_APP ) {
            try {
                 window.prompt(_url, "");
            } catch (ex) {
                console.log( ex );
            }
        } else {
            console.log( 'no native, no method ...' );
            // let _tempParams = JSON.parse(_params);
            // if( _method === 'toast' ) {
            //     Toast(_tempParams);
            // } else if( _method === 'alert' ) {
            //     MessageBox(_tempParams);
            // } else if( _method === 'confirm' ) {
            //     MessageBox.confirm(_tempParams.message, _tempParams.title).then(action => {
            //         window.JSBridge.callback(_callbackId, '000', { action: 1 });
            //     }, action => {
            //         window.JSBridge.callback(_callbackId, '000', { action: 0 });
            //     });
            // }
        }
    },
    /*
    callbackId id
    status
    params
    keepCallback: callback
    */
    // login, pageEnter,
    callback: function( callbackId, status, params, keepCallback ){
        try {
            let _callback = this.callbackList[ callbackId ];
            console.log(`callbackId: ${callbackId}, params: ${JSON.stringify(params)}, callback: ${_callback}`);
            if( !_callback ) {
                console.log( 'callback is null' );
                return;
            }
            _callback && _callback( params );
            if( keepCallback !== true ) {
                delete this.callbackList[ callbackId ];
            }
        } catch( ex ) {
            console.log(`callbackId: ${callbackId}, params: ${JSON.stringify(params)}, ex: ${ex}`);
            throw ex;
        }
    },
    //  APP 
    onAPP: function( method, params ){
        console.log(`method: ${method}, params: ${JSON.stringify(params)}`);
        if( !this.subEventList[ method ] ){
            console.log(`${method} no sub`);
        }
        this.subEventList[ method ].forEach(( fun )=>{
            fun.call(this, params );
        });
    },
    subEvent: function(method, callback ){
        if( !method ) {
            console.log( 'method is not string' );
            return;
        }
        if( callback && typeof callback !== 'function' ){
            console.log( 'callback is not function' );
            return;
        }
        if( !this.subEventList[ method ] ){
            this.subEventList[ method ] = [];
        }
        this.subEventList[ method ].push( callback );
        console.log( this.subEventList );
    }
};
window[JSBRIDGE_NAME] = JSBridge;
export default JSBridge;


