<template>
    <div>
        <div class="transfer-money">
            <span class="transfer-money-span">{{title}}</span>
            <div class="mint-cell mint-field is-nolabel">
                <div class="mint-cell-wrapper">
                    <div class="mint-cell-value">
                        <label class="transfer-money-label"></label>
                        <input id="m-input" class="mint-field-core" type="text" :placeholder="placeholder" :value="currentValue" :totalMoney="totalMoney"
                            @input="handleInput" @blur="handleBlur" @focus="handleFocus">
                        <hr class="transfer-hr" />
                        <div class="mint-field-clear mint-field-clear-id" style="display: none;" @click="fclear">
                            <i class="mintui mintui-field-error"></i>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <span class="svalidate" id="svalidate">{{validate}}</span>
    </div>
</template>

<script>
    export default {
        props: {
            title: {
                type: String,
                default: ''
            },
            totalMoney: '',
            placeholder: {
                type: String,
                default: ''
            },
            validate: {
                type: String,
                default: ''
            },
            twoValidate: '',
            value: {}
        },
        data() {
            return {
                money: '',
                currentValue: this.value
            };
        },
        watch: {
            value(val) {
                this.currentValue = val;
            },
            currentValue(val) {
                this.handleFocus();
                this.$emit('input', val);
            }
        },
        methods: {
            handleInput(evt) {
                this.currentValue = evt.target.value;
            },
            fclear() {
                this.currentValue = '';
                document.getElementById('svalidate').style.display = 'none';
                document.getElementById('m-input').className = 'mint-field-core';
            },
            handleFocus() {
                var c = document.getElementsByClassName('mint-field-clear-id');
                c[0] != null ? c[0].style.display = 'block' : '';
                if (this.currentValue != '') {
                    c[0] != null ? c[0].style.display = 'block' : '';
                } else {
                    c[0] != null ? c[0].style.display = 'none' : '';
                }
                this.$emit('focus');
            },
            handleBlur() {
                var c = document.getElementsByClassName('mint-field-clear-id');
                var totalMoney = this.totalMoney;
                if (Number(this.currentValue) > Number(totalMoney)) {
                    document.getElementById('svalidate').style.display = 'inline-block';
                    document.getElementById('m-input').className = 'mint-field-core mint-field-core-color';
                    if (this.twoValidate == 'show') {
                        document.getElementById('svalidate').className = 'svalidate svalidate-two';
                    }
                } else {
                    document.getElementById('svalidate').style.display = 'none';
                    document.getElementById('m-input').className = 'mint-field-core';
                }
                this.$emit('blur');
            }
        }
    };

</script>

<style lang="scss" scoped>
    .transfer-money {
        background: #fff;
        width: 750px;
        height: 230px;
        .transfer-money-span {
            display: block;
            padding: 40px 0px 24px 50px;
            font-family: PingFangSC-Regular;
            font-size: 16px;
            color: #272727;
            letter-spacing: 0;
        }
        .transfer-money-label {
            font-family: PingFangSC-Regular;
            font-size: 20px;
            color: #272727;
            letter-spacing: 0;
            padding-right: 20px;
        }
        .transfer-hr {
            border: 0.5px solid #E4E5E6;
            position: absolute;
            left: 100px;
            top: 95px;
            right: 50px;
        }
    }

    .mint-field-core-color {
        color: #FF511A;
    }

    .svalidate {
        display: none;
        margin: 20px 0px 0px 44px;
        opacity: 0.8;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #FF511A;
        line-height: 22px;
    }

    .svalidate-two {
        position: absolute;
    }
</style>


