const state = {
    search: {
        paymentAccountList: [],
        checked: {},
        patternName: '',
        TRAN_TYPE: '0', //  0- 1-
        COR_NAME: '', // 
        PAYEE_ACCOUNT: '', // 
        PAYER_ACCOUNT: '', // 
        CURRENCY: '0', //  0-
        AMOUNT: '', // 
        FEE: '', // 
        SENDMENO: '', // 
        ISMOBILETRAN: '0', // 
        PAY_PWD: '', // 
        MSGCODE: '' // 
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateInternalTransferSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearInternalTransferSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            checked: {},
            patternName: '',
            TRAN_TYPE: '0',
            COR_NAME: '',
            PAYEE_ACCOUNT: '',
            PAYER_ACCOUNT: '',
            CURRENCY: '0',
            AMOUNT: '',
            FEE: '',
            SENDMENO: '',
            ISMOBILETRAN: '',
            PAY_PWD: '',
            MSGCODE: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



