const state = {
    search: {
        paymentAccountList: [],
        checked: {},
        patternName: '',
        TRAN_TYPE: '0', //  0- 1-
        COR_NAME: '', // 
        PAYEE_ACCOUNT: '', // 
        COR_BANKNUMBER: '', // 
        COR_BANKNAME: '', // 
        PAYER_ACCOUNT: '', // 
        CURRENCY: '0', //  0-
        AMOUNT: '', // 
        FEE: '', // 
        PAY_PWD: '',  // 
        SENDMENO: '', // 
        payeeBankName: '',
        bankCity: '',
        bankBranch: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateAgriculturalCreditTransferSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearAgriculturalCreditTransferSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            paymentAccountList: [],
            checked: {},
            patternName: '',
            TRAN_TYPE: '0',
            COR_NAME: '',
            PAYEE_ACCOUNT: '',
            COR_BANKNUMBER: '',
            COR_BANKNAME: '',
            PAYER_ACCOUNT: '',
            CURRENCY: '0',
            AMOUNT: '',
            FEE: '',
            PAY_PWD: '',
            SENDMENO: '',
            payeeBankName: '',
            bankCity: '',
            bankBranch: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



