<template>
    <div>
        <div class="fund-card" @click="cardQueryClick" v-if="cardType != '2'">
            <span>{{name}}</span>
            <span>{{account}}</span>
            <span>{{bankName}}</span>
        </div>
        <div class="fund-card fund-card-detail" v-if="cardType == '2'">
            <span>{{name}}</span>
            <span>{{account}}</span>
            <span>{{bankName}}</span>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        name: '',
        account: '',
        bankName: '',
        cardType: ''
    },
    data() {
        return {};
    },
    methods: {
        cardQueryClick () {
            this.$emit('card-query-click');
        }
    }
};

</script>

<style lang="scss" scoped>
.fund-card {
    height: 216px;
    width: 710px;
    border-radius: 10px;
    background-image: url('../../assets/img/bank/card/deposit-card@2x.png');
    background-image: image-set(url('../../assets/img/bank/card/deposit-card@3x.png') 3x);
    background-repeat: no-repeat;
    background-size: 100%;
    margin: 37px auto 0px auto;
    span {
        display: block;
        padding-top: 40px;
        margin-left: 40px;
    }
    span:nth-child(1) {
        font-size: 15px;
        color: #333333;
    }
    span:nth-child(2) {
        font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
        padding-top: 10px;
        font-size: 14px;
        color: #444857;
        letter-spacing: 2px;
    }
    span:nth-child(3) {
        padding-top: 18px;
        opacity: 0.8;
        font-size: 13px;
        color: #333333;
        letter-spacing: 0;
    }
}
.fund-card-detail {
    width: 100%;
    border-radius: 0px;
    margin: 0 auto;
    background-image: url('../../assets/img/bank/card/deposit-card-detail@2x.png');
    background-image: image-set(url('../../assets/img/bank/card/deposit-card-detail@3x.png') 3x);
    span {
        display: block;
        padding-top: 30px;
        margin-left: 40px;
    }
}
</style>


